import { useEffect, useState } from 'react';
import { useParams, useNavigate, useSearchParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setCurrStationProducts } from '../store/actions/category.actions';
import * as XLSX from 'xlsx';

export function SubCategoryDetails(props) {
    const params = useParams();
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [filterValue, setFilterValue] = useState('');
    const [filterType, setFilterType] = useState('arabic');
    const [selectedProducts, setSelectedProducts] = useState([]);

    const currProducts = useSelector(
        (storeState) => storeState.categoryModule.currProducts
    );

    const [searchParams] = useSearchParams();

    useEffect(() => {
        const subId = searchParams.get('subId');
        const catId = searchParams.get('catId');
        // subId: subId,
        // catId: params.id,
        // alert("1");
        dispatch(setCurrStationProducts(catId, subId));

        // loadStationProducts();
    }, []);
    // console.log("params: " + params.id);
    // function loadStationProducts() {
    // }

    if (!currProducts) return (
        <div className="centered-container">
            <div className="loader"></div>
        </div>
    );
    console.log("dsads")
    // console.log("station: " + station)
    // console.log("station.subCategories: " + station.subCategories)
    // const subCategory = station.subCategories.find(sc => sc.subCategoryId === params.id);
    // if (!subCategory) return (
    //     <div className="centered-container">
    //         <div className="loader"></div>
    //     </div>
    // );

    const filteredProducts = currProducts.filter((product) => {
        switch (filterType) {
            case 'arabic':
            case 'english':
            case 'hebrew':
            case 'french':
                return product[`productNameIn${capitalizeFirstLetter(filterType)}`]
                    .toLowerCase()
                    .includes(filterValue.toLowerCase());
            case 'price':
                return filterValue === '' || product.productPrice <= parseFloat(filterValue);
            case 'parcode':
                return filterValue === '' || product.productParcode === parseInt(filterValue);
            default:
                return true;
        }
    });

    function capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    function handleCheckboxChange(productId) {
        if (selectedProducts.includes(productId)) {
            setSelectedProducts(selectedProducts.filter(id => id !== productId));
        } else {
            setSelectedProducts([...selectedProducts, productId]);
        }
    }
    function downloadExcel() {
        const selectedProductsDetails = currProducts
            .filter(product => selectedProducts.includes(product.productId))
            .map(product => ({
                productParcode: product.productParcode,
                productNameInArabic: product.productNameInArabic,
                productNameInEnglish: product.productNameInEnglish,
                productNameInHebrew: product.productNameInHebrew,
                productNameInFrench: product.productNameInFrench,
                productDescInArabic: product.productDescInArabic,
                productDescInEnglish: product.productDescInEnglish,
                productDescInHebrew: product.productDescInHebrew,
                productDescInFrench: product.productDescInFrench,
                categoryNameInArabic: product.categoryNameInArabic,
                categoryNameInEnglish: product.categoryNameInEnglish,
                categoryNameInHebrew: product.categoryNameInHebrew,
                categoryNameInFrench: product.categoryNameInFrench,
                subCategoryNameInArabic: product.subCategoryNameInArabic,
                subCategoryNameInEnglish: product.subCategoryNameInEnglish,
                subCategoryNameInHebrew: product.subCategoryNameInHebrew,
                subCategoryNameInFrench: product.subCategoryNameInFrench,
                productPrice: product.productPrice,
                productImgUrl: product.productImgUrl,
            }));

        const worksheet = XLSX.utils.json_to_sheet(selectedProductsDetails);
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Products');
        XLSX.writeFile(workbook, 'selected_products.xlsx');
    }

    function clearSelectedProducts() {
        setSelectedProducts([]);
    }

    return (
        <>
            <div className='sub-categories-actions-div'>
                <div className='filter-div'>
                    <button onClick={downloadExcel}>Download Excel</button>
                    <button onClick={clearSelectedProducts}>Clear Selected Products</button>
                    <select
                        value={filterType}
                        onChange={(e) => {
                            setFilterType(e.target.value);
                            setFilterValue('');
                        }}
                    >
                        <option value='arabic'>Arabic</option>
                        <option value='english'>English</option>
                        <option value='hebrew'>Hebrew</option>
                        <option value='french'>French</option>
                        <option value='price'>Price</option>
                        <option value='parcode'>Parcode</option>
                    </select>
                    {/* <input
                        type={filterType === 'price' || filterType === 'parcode' ? 'number' : 'text'}
                        value={filterValue}
                        onChange={(e) => setFilterValue(e.target.value)}
                        placeholder={`Filter by ${filterType}`}
                    /> */}
                </div>
                <section className="filter-field">

                    <fieldset>
                        <legend htmlFor="name" className="black">
                            search product
                        </legend>

                        <input
                            autoFocus
                            className='filter-input'
                            type={filterType === 'price' || filterType === 'parcode' ? 'number' : 'text'}
                            value={filterValue}
                            onChange={(e) => setFilterValue(e.target.value)}
                            placeholder={`Filter by ${filterType}`}
                        />
                    </fieldset>

                </section>
            </div>
            <section className='robot-index'>
                <section className='robot-list simple-cards-grid'>
                    {filteredProducts.map((product) => (
                        <article key={product.productId} className='catalog-preview pointer'>
                            <div className='image-container'>
                                <img
                                    className='preview-image'
                                    src={product.productImgUrl}
                                    alt={product.productNameInEnglish}
                                />
                            </div>
                            <p className='preview-p'>{product[`productNameIn${capitalizeFirstLetter(filterType)}`]}</p>
                            <p>{product.productPrice}</p>
                            <input
                                type='checkbox'
                                checked={selectedProducts.includes(product.productId)}
                                onChange={() => handleCheckboxChange(product.productId)}
                            />
                        </article>
                    ))}
                </section>
            </section>
        </>
    );
}