import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addProductToCategory, setCurrStation } from '../../store/actions/category.actions';
import { uploadImg } from '../../services/upload.service';
import { makeId } from '../../services/util.service';
export default function AddPdfPageModal({ closeModal }) {

    const categories = useSelector((storeState) => storeState.categoryModule.robots)
    const [currentLanguage, setCurrentLanguage] = useState('English');
    const [filteredCategories, setFilteredCategories] = useState([]);
    const [filteredSubCategories, setFilteredSubCategories] = useState([]);
    const selectedCategory = useSelector((storeState) => storeState.categoryModule.currStation)
    // const [selectedCategory, setSelectedCategory] = useState(null);
    const [formValues, setFormValues] = useState({
        categoryEnglish: '',
        categoryFranch: '',
        categoryHebrew: '',
        categoryArabic: '',
        subcategoryArabic: '',
        subcategoryHebrew: '',
        subcategoryEnglish: '',
        subcategoryFranch: '',
        productPrice: '',
        productParcode: '',
        productImage: '',
        productNameArabic: '',
        productNameFranch: '',
        productNameHebrew: '',
        productNameEnglish: '',
        productDescInEnglish: "",
        productDescInFranch: "",
        productDescInArabic: "",
        productDescInHebrew: "",

    });
    const [isUploading, setIsUploading] = useState(false);
    const handleChange = (e) => {
        console.log(e.target.value)
        console.log("formValues", formValues)
        setFormValues({
            ...formValues,
            [e.target.name]: e.target.value,
        });
    };

    useEffect(() => {
        if (currentLanguage && Array.isArray(categories)) {
            const searchField = `categoryNameIn${currentLanguage}`;
            const filtered = categories.filter((category) => {
                // Check if category[searchField] is a string and if formValues[`category${currentLanguage}`] is defined
                const categoryValue = category[searchField];
                const formValue = formValues[`category${currentLanguage}`];
                return typeof categoryValue === 'string' &&
                    typeof formValue === 'string' &&
                    categoryValue.toLowerCase().includes(formValue.toLowerCase());
            });
            setFilteredCategories(filtered);
        }
    }, [formValues, currentLanguage, categories]);


    const handleCategorySelect = (category, id) => {
        dispatch(setCurrStation(id))
        // console.log("categorySelect", category)
        setFormValues(prev => ({
            ...prev,
            [`category${currentLanguage}`]: category
        }));
        // setSelectedCategory(category);
        setFilteredCategories([]);
    };
    const dispatch = useDispatch()
    const handleFormSubmit = (e) => {
        e.preventDefault();
        const product = {
            productId: makeId(),
            productNameInEnglish: formValues.productNameEnglish,
            productNameInArabic: formValues.productNameArabic,
            productNameInHebrew: formValues.productNameHebrew,
            productNameInFranch: formValues.productNameFranch,
            productDescInEnglish: formValues.productDescInEnglish,
            productDescInFranch: formValues.productDescInFranch,
            productDescInArabic: formValues.productDescInArabic,
            productDescInHebrew: formValues.productDescInHebrew,
            productImgUrl: formValues.productImage,
            productParcode: formValues.productParcode,
            productPrice: formValues.productPrice,
            categoryNameInArabic: formValues.categoryArabic,
            categoryNameInEnglish: formValues.categoryEnglish,
            categoryNameInHebrew: formValues.categoryHebrew,
            categoryNameInFrench: formValues.categoryFranch,
            subCategoryNameInEnglish: formValues.subcategoryEnglish,
            subCategoryNameInHebrew: formValues.subcategoryHebrew,
            subCategoryNameInArabic: formValues.subcategoryArabic,
            subCategoryNameInFrench: formValues.subcategoryFranch,
        }

        dispatch(addProductToCategory(formValues.categoryEnglish, product, formValues.subcategoryEnglish))
        closeModal()

    };

    const handelFile = async (e) => {
        const file = e.target.files[0];
        try {
            setIsUploading(true);
            const { url } = await uploadImg(file);
            console.log("url", url);
            setFormValues({ ...formValues, productImage: url });
        } catch (err) {
            console.log('Error uploading image: ', err);
        } finally {
            setIsUploading(false);
        }
    };

    useEffect(() => {
        console.log("hi use effect")
        if (selectedCategory && formValues[`subcategory${currentLanguage}`] && Array.isArray(selectedCategory.subCategories)) {
            const subCategoryField = `subCategoryNameIn${currentLanguage}`;
            console.log("selectedCategory", selectedCategory)
            const filtered = selectedCategory.subCategories.filter((subCategory) => {
                const subCategoryValue = subCategory[subCategoryField];
                const formValue = formValues[`subcategory${currentLanguage}`];
                return typeof subCategoryValue === 'string' &&
                    typeof formValue === 'string' &&
                    subCategoryValue.toLowerCase().includes(formValue.toLowerCase());
            });
            setFilteredSubCategories(filtered);
        } else {
            setFilteredSubCategories([]);
        }
    }, [formValues[`subcategory${currentLanguage}`], selectedCategory, currentLanguage]);



    return (
        <div className="modal-backdrop" >
            <div className="modal-add" >

                <div>
                    <button className='close-product-modal-btn' onClick={closeModal}>x</button>
                </div>
                <div className="modal-header">
                    <a onClick={() => setCurrentLanguage('Arabic')}></a>
                    <a onClick={() => setCurrentLanguage('English')}>ENGLISH</a>
                    <a onClick={() => setCurrentLanguage('Hebrew')}></a>
                    <a onClick={() => setCurrentLanguage('Franch')}>FRANCAISE</a>
                </div>

                <form className="modal-body" onSubmit={handleFormSubmit}>

                    <div className="image-section">
                        <label>
                            product image url
                            <input
                                type="text"
                                name={"productImage"}
                                // value={formValues.productPrice}
                                value={formValues.productImage}
                                onChange={handleChange}
                            />
                        </label>
                        <h1>OR</h1>
                        <label>
                            {isUploading ? (
                                //    <div className="centered-container-img">
                                <span className="img-loader"></span>
                                //    </div>
                            ) : (
                                <img
                                    src={formValues.productImage || "https://www.free-videoconverter.net/images/free-online-image-enhancer/upload-image.png"}
                                    alt="product"
                                />
                            )}

                            <input type="file" onChange={handelFile} className='hidden' />
                        </label>
                    </div>


                    <div className="product-details-section">
                        <h1>Product details</h1>
                        <label>
                            Product Name ({currentLanguage})
                            <input
                                type="text"
                                name={`productName${currentLanguage}`}
                                value={formValues[`productName${currentLanguage}`]}
                                onChange={handleChange}
                            />
                        </label>
                        <label>
                            Product Description ({currentLanguage})
                            <textarea
                                name={`productDescIn${currentLanguage}`}
                                value={formValues[`productDescIn${currentLanguage}`]}
                                onChange={handleChange}
                            ></textarea>
                        </label>
                    </div>

                    <div className="category-details-section">

                        <label>
                            Category Name ({currentLanguage})
                            <input
                                type="text"
                                name={`category${currentLanguage}`}
                                value={formValues[`category${currentLanguage}`]}
                                onChange={handleChange}
                                autoComplete="off"
                            />
                            {filteredCategories.length > 0 && formValues[`category${currentLanguage}`] && (
                                <ul className="category-options">
                                    {filteredCategories.map((category) => (
                                        <li
                                            key={category._id}
                                            onClick={() => handleCategorySelect(category[`categoryNameIn${currentLanguage}`], category._id)}
                                        >
                                            {category[`categoryNameIn${currentLanguage}`]}
                                        </li>
                                    ))}
                                </ul>
                            )}

                        </label>

                        <label>
                            Subcategory Name ({currentLanguage})
                            <input
                                type="text"
                                name={`subcategory${currentLanguage}`}
                                value={formValues[`subcategory${currentLanguage}`]}
                                onChange={handleChange}
                                autoComplete="off"
                            />
                            {filteredSubCategories.length > 0 && (
                                <ul className="category-options">
                                    {filteredSubCategories.map((subCategory) => (
                                        <li
                                            key={subCategory.subCategoryId}
                                            onClick={() => {
                                                setFormValues(prev => ({
                                                    ...prev,
                                                    [`subcategory${currentLanguage}`]: subCategory[`subCategoryNameIn${currentLanguage}`]
                                                }));
                                                setFilteredSubCategories([]);
                                            }}
                                        >
                                            {subCategory[`subCategoryNameIn${currentLanguage}`]}
                                        </li>
                                    ))}
                                </ul>
                            )}
                        </label>
                        <label>
                            Product Price
                            <input
                                type="text"
                                name="productPrice"
                                value={formValues.productPrice}
                                onChange={handleChange}
                            />
                        </label>
                    </div>


                    <button className='add-product-modal-btn' type="submit">Add Product</button>
                </form>
            </div >
        </div >
    );
}

