async function addProduct(categoryName, product, subCategoryName) {
    console.log("station/${categoryName}/category/${subCategoryName}/product")

    return httpService.post(`station/${categoryName}/category/${subCategoryName}/product`, { product })
}
async function getCategoryById(id) {
    const res = await httpService.get(`station/${id}`)
    console.log("res", res)
    return res


}
async function getCategoryProductsById(catId, subId) {
    // const res1 = await getCategoryById(id)
    const res = await httpService.get(`station/${catId}/subCategory/${subId}`)
    // const products = 
    // console.log("res", products)
    return res


}

async function createNewCatalog(categoryNameInEnglish, categoryNameInHebrew, categoryNameInArabic, categoryNameInFranch, categoryImgUrl) {
    // let loggedinUser = await userService.getLoggedinUser()
    const newCatalog = {
        categoryNameInEnglish,
        categoryNameInHebrew,
        categoryNameInArabic,
        categoryNameInFranch,
        categoryImgUrl,
        subCategories: [],

    }

    return httpService.post('station', newCatalog)
}
async function createNewSubCategory(categoryNameInEnglish, subCategoryNameInEnglish, subCategoryNameInHebrew, subCategoryNameInArabic, subCategoryNameInFranch, subCategoryImgUrl) {
    // let loggedinUser = await userService.getLoggedinUser()
    const newSubCategory = {
        subCategoryId: makeId(10),
        subCategoryNameInEnglish,
        subCategoryNameInHebrew,
        subCategoryNameInArabic,
        subCategoryNameInFranch,
        subCategoryImgUrl,
        products: [],

    }

    return httpService.post(`station/${categoryNameInEnglish}/add/subCategory`, { newSubCategory })
    // return httpService.post('station', newCatalog)
}
