import { useEffect, useState } from 'react'
import { RobotList } from '../cmps/RobotList'

import { useDispatch, useSelector } from 'react-redux'
import { loadRobots } from '../store/actions/category.actions'
import AddPdfPageModal from '../cmps/Modals/AddPdfPageModal'


export function RobotIndex(props) {
    const [showModal, setShowModal] = useState(false)
    const robots = useSelector((storeState) => storeState.categoryModule.robots)

    const dispatch = useDispatch()



    useEffect(() => {
        dispatch(loadRobots())
    }, [])


    // function openAddModal() {
    //     setShowModal(true)
    // }
    // function handleCloseModal() {
    //     setShowModal(false)
    // }


    if (!robots) return (
        <div className="centered-container">
            <div className="loader"></div>
        </div>
    )

    return (
        <section className='robot-index'>
            {/* <button onClick={openAddModal}>
                +add
            </button> */}
            <RobotList robots={robots} />
            {/* {showModal && <AddPdfPageModal closeModal={handleCloseModal} />} */}
        </section>
    )
}
