import React, { useState } from 'react';
import { useDispatch } from 'react-redux';

import { uploadImg } from '../../services/upload.service';
import { addCategory } from '../../store/actions/category.actions';

export function AddCategoryModal({ closeModal }) {
    const [formValues, setFormValues] = useState({
        categoryNameEnglish: '',
        categoryNameHebrew: '',
        categoryNameArabic: '',
        categoryNameFrench: '',
        categoryImgUrl: '',
    });

    const [isUploading, setIsUploading] = useState(false);

    const dispatch = useDispatch()

    const handleChange = (e) => {
        setFormValues({
            ...formValues,
            [e.target.name]: e.target.value,
        });
    };

    const handleFormSubmit = (e) => {
        e.preventDefault();

        dispatch(addCategory(formValues.categoryNameEnglish, formValues.categoryNameHebrew, formValues.categoryNameArabic, formValues.categoryNameFrench, formValues.categoryImgUrl))
        closeModal()
    };

    const handelFile = async (e) => {
        const file = e.target.files[0];
        try {
            setIsUploading(true);
            const { url } = await uploadImg(file);
            console.log("url", url);
            setFormValues({ ...formValues, categoryImgUrl: url });
        } catch (err) {
            console.log('Error uploading image: ', err);
        } finally {
            setIsUploading(false);
        }
    };

    return (
        <div className="modal-backdrop" >
            <div className="modal-add" >

                <div>
                    <button className='close-product-modal-btn' onClick={closeModal}>x</button>
                </div>

                <form className="modal-body" onSubmit={handleFormSubmit}>

                    <div className="image-section">
                        <label>
                            Category image url
                            <input
                                type="text"
                                name="categoryImgUrl"
                                value={formValues.categoryImgUrl}
                                onChange={handleChange}
                            />
                        </label>
                        <h1>OR</h1>
                        <label>
                            {isUploading ? (
                                // <div className="centered-container-img">
                                <span className="img-loader"></span>
                                // </div>
                            ) : (
                                <img
                                    src={formValues.categoryImgUrl || "https://www.free-videoconverter.net/images/free-online-image-enhancer/upload-image.png"}
                                    alt="category"
                                />
                            )}

                            <input type="file" onChange={handelFile} className='hidden' />
                        </label>
                    </div>


                    <div className="category-details-section">
                        <h1>Category details</h1>

                        <label>
                            Category Name in English
                            <input
                                type="text"
                                name="categoryNameEnglish"
                                value={formValues.categoryNameEnglish}
                                onChange={handleChange}
                            />
                        </label>

                        <label>
                            Category Name in Hebrew
                            <input
                                type="text"
                                name="categoryNameHebrew"
                                value={formValues.categoryNameHebrew}
                                onChange={handleChange}
                            />
                        </label>

                        <label>
                            Category Name in Arabic
                            <input
                                type="text"
                                name="categoryNameArabic"
                                value={formValues.categoryNameArabic}
                                onChange={handleChange}
                            />
                        </label>

                        <label>
                            Category Name in French
                            <input
                                type="text"
                                name="categoryNameFrench"
                                value={formValues.categoryNameFrench}
                                onChange={handleChange}
                            />
                        </label>
                    </div>


                    <button className='add-product-modal-btn' type="submit">Add Category</button>
                </form>
            </div>
        </div>
    );
}
