import { Route, HashRouter as Router, Routes, Switch, useLocation } from 'react-router-dom';
import './assets/scss/global.scss'
import { AppHeader } from './cmps/AppHeader'
import { RobotIndex } from './views/RobotIndex'
import { RobotDetails } from './views/RobotDetails';
import { SubCategoryDetails } from './views/SubCategoryDetails';
import { Login } from './views/Login';
import { UserMsg } from './cmps/UserMsg';
import { getUser } from './store/actions/user.actions';
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';


function MainApp() {
    const location = useLocation()

    const dispatch = useDispatch()

    useEffect(() => {
        const user = dispatch(getUser())
        console.log('user', user)
    }, [])
    return (
        <section className="main-app">
            <UserMsg />
            {location.pathname !== '/' && <AppHeader />}
            <main className="container">
                <Routes>
                    <Route path="/" element={<Login />} />
                    <Route path="/home" element={<RobotIndex />} />
                    <Route path="/station/:id" element={<RobotDetails />} />
                    <Route path="/products" element={<SubCategoryDetails />} />
                </Routes>
            </main>
        </section>
    )
}


function App() {
    return (
        <Router>
            <MainApp />
        </Router>
    )
}

export default App;
