import { robotService } from "../../services/robot.service"
import { REMOVE_ROBOT, SET_FILTER_BY, SET_ROBOTS, SET_CURR_STATION, ADD_PRODUCT, ADD_CATEGORY, ADD_SUBCATEGORY, SET_CURR_STATION_PRODUCTS } from "../reducers/category.reducer"

export function loadRobots() {
    return async (dispatch, getState) => {
        try {
            const robots = await robotService.query()
            const action = {
                type: SET_ROBOTS,
                robots
            }
            dispatch(action)
        } catch (error) {
            console.log('error:', error)
        }
    }
}
export function addProductToCategory(categoryName, product, subCategoryName) {
    return async (dispatch, getState) => {
        try {
            const newProduct = await robotService.addProduct(categoryName, product, subCategoryName)
            const action = {
                type: ADD_PRODUCT,
                product
            }
            dispatch(action)
        } catch (error) {
            console.log('error:', error)
        }
    }
}
export function addCategory(categoryNameInEnglish, categoryNameInHebrew, categoryNameInArabic, categoryNameInFranch, categoryImgUrl) {
    return async (dispatch, getState) => {
        try {
            const category = await robotService.createNewCatalog(categoryNameInEnglish, categoryNameInHebrew, categoryNameInArabic, categoryNameInFranch, categoryImgUrl)
            const action = {
                type: ADD_CATEGORY,
                category
            }
            dispatch(action)
        } catch (error) {
            console.log('error:', error)
        }
    }
}
export function addSubCategory(categoryNameInEnglish, subCategoryNameInEnglish, subCategoryNameInHebrew, subCategoryNameInArabic, subCategoryNameInFranch, subCategoryImgUrl) {
    return async (dispatch, getState) => {
        try {
            const subCategory = await robotService.createNewSubCategory(categoryNameInEnglish, subCategoryNameInEnglish, subCategoryNameInHebrew, subCategoryNameInArabic, subCategoryNameInFranch, subCategoryImgUrl)
            const action = {
                type: ADD_SUBCATEGORY,
                subCategory,
                categoryNameInEnglish
            }
            dispatch(action)
        } catch (error) {
            console.log('error:', error)
        }
    }
}
export function setCurrStation(id) {
    return async (dispatch, getState) => {
        try {

            const action1 = {
                type: SET_CURR_STATION,
                station: null,
            }
            dispatch(action1)
            const station = await robotService.getCategoryById(id)

            const action = {
                type: SET_CURR_STATION,
                station,
            }
            dispatch(action)
        } catch (error) {
            console.log('error:', error)
        }
    }
}
export function setCurrStationProducts(catId, subId) {
    return async (dispatch, getState) => {
        try {

            const action1 = {
                type: SET_CURR_STATION_PRODUCTS,
                products: null,
            }
            dispatch(action1)
            const products = await robotService.getCategoryProductsById(catId, subId)

            const action = {
                type: SET_CURR_STATION_PRODUCTS,
                products,
            }
            dispatch(action)
        } catch (error) {
            console.log('error:', error)
        }
    }
}
export function removeRobot(robotId) {
    return async (dispatch) => {
        try {
            await robotService.remove(robotId)
            const action = { type: REMOVE_ROBOT, robotId }
            dispatch(action)
            return 'Removed!'
        } catch (error) {
            console.log('error:', error)
        }
    }
}

export function setFilterBy(filterBy) {
    return (dispatch) => {
        dispatch({ type: SET_FILTER_BY, filterBy })
    }
}