
export const SET_ROBOTS = 'SET_ROBOTS'
export const ADD_ROBOT = 'ADD_ROBOT'
export const REMOVE_ROBOT = 'REMOVE_ROBOT'
export const UPDATE_ROBOT = 'UPDATE_ROBOT'
export const SET_FILTER_BY = 'SET_FILTER_BY'
export const SET_CURR_STATION = 'SET_CURR_STATION'
export const ADD_PRODUCT = 'ADD_PRODUCT'
export const ADD_CATEGORY = 'ADD_CATEGORY'
export const ADD_SUBCATEGORY = 'ADD_SUBCATEGORY'
export const SET_CURR_STATION_PRODUCTS = 'SET_CURR_STATION_PRODUCTS'

const INITIAL_STATE = {
    robots: null,
    currStation: null,
    currStationId: "",
    currProducts: null
}

export function categoryReducer(state = INITIAL_STATE, action = {}) {

    switch (action.type) {
        case SET_ROBOTS:
            return {
                ...state,
                robots: action.robots
            }
        case ADD_SUBCATEGORY:
            return {
                ...state,
                robots: state.robots.map((c) =>
                    c.categoryNameInEnglish === action.categoryNameInEnglish
                        ? { ...c, subCategories: [...c.subCategories, action.subCategory] }
                        : c
                ),
            }
        case ADD_CATEGORY:
            return {
                ...state,
                robots: [...state.robots, action.category]
            }
        case REMOVE_ROBOT:
            return {
                ...state,
                robots: state.robots.filter(robot => robot._id !== action.robotId)
            }
        case UPDATE_ROBOT:
            return {
                ...state,
                robots: state.robots.map(robot => robot._id === action.robot._id ? action.robot : robot)
            }
        case SET_FILTER_BY:
            return {
                ...state,
                filterBy: { ...action.filterBy }
            }
        case SET_CURR_STATION:
            return {
                ...state,
                currStation: action.station,
                // currStationId: action.station._id,
                //   currStationImg: action.station.imgUrl,
            }
        case SET_CURR_STATION_PRODUCTS:
            return {
                ...state,
                currProducts: action.products,
                // currStationId: action.station._id,
                //   currStationImg: action.station.imgUrl,
            }

        default:
            return state;
    }
}