import React from 'react'
import { useNavigate } from 'react-router-dom'
import { setCurrStation } from '../store/actions/category.actions'
import { useDispatch } from 'react-redux'

export function RobotPreview({ robot, onRemoveRobot }) {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    function goToDetails() {
        console.log("hi")
        console.log(robot)
        dispatch(setCurrStation(robot._id))
        navigate(`/station/${robot._id}`)
    }

    return (
        <article
            onClick={goToDetails}
            className="catalog-preview pointer"
        >
            <div className="image-container">
                <img className="preview-image" src={robot.categoryImgUrl} />
            </div>
            <p className="preview-p">{robot.categoryNameInEnglish}</p>
        </article>
    )
}
