import { useSelector } from "react-redux";
import { NavLink, useNavigate, } from "react-router-dom";
import AddPdfPageModal from "./Modals/AddPdfPageModal";
import { useState } from "react";
import { AddCategoryModal } from "./Modals/AddCategoryModal";
import { AddSubCategoryModal } from "./Modals/AddSubCategoryModal";

export function AppHeader() {
    const [showModal, setShowModal] = useState(false)
    const [showCatalogModal, setShowCatalogModal] = useState(false)
    const [showSubCatalogModal, setShowSubCatalogModal] = useState(false)


    function openAddModal() {
        setShowModal(true)
    }
    function openAddCatalogModal() {
        setShowCatalogModal(true)
    }
    function openAddSubCatalogModal() {
        setShowSubCatalogModal(true)
    }

    function handleCloseModal() {
        setShowModal(false)
    }

    function handleCloseCategoryModal() {
        setShowCatalogModal(false)
    }
    function handleCloseSubCategoryModal() {
        setShowSubCatalogModal(false)
    }


    function goToCategories() {
        navigate('/home');
    }


    const navigate = useNavigate()



    return (
        <header className="app-header">
            <section className="first-section pointer" onClick={goToCategories}>
                <div>
                    <img src="data:image/jpeg;base64," />
                </div>
                <h1 className="logo">Haat</h1>
            </section>
            <section className="second-section">
                <button className="nice-btn" onClick={openAddModal}>Add product</button>
                <button className="nice-btn" onClick={openAddCatalogModal}>Add Categorys</button>
                <button className="nice-btn" onClick={openAddSubCatalogModal}>Add Subcategory</button>
            </section>
            {/* <section className="thierd section">
                <h1 className="logo">Haat</h1>
            </section> */}
            {showModal && <AddPdfPageModal closeModal={handleCloseModal} />}
            {showCatalogModal && <AddCategoryModal closeModal={handleCloseCategoryModal} />}
            {showSubCatalogModal && <AddSubCategoryModal closeModal={handleCloseSubCategoryModal} />}
        </header>
    )
}

