import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'



import Confetti from 'react-dom-confetti'
import { NotEyeIcon } from '../cmps/Svgs/NotEyeIcon'
import { EyeIcon } from '../cmps/Svgs/EyeIcon'

import { ValidationModal } from '../cmps/Modals/ValidationModal'
import { doLogin } from '../store/actions/user.actions'
import { useNavigate } from 'react-router-dom'


export function Login() {
  const navigate = useNavigate()

  const [LoginCred, setLoginCred] = useState({
    username: '',
    password: '',
    img: 'https://img.freepik.com/free-vector/businessman-character-avatar-isolated_24877-60111.jpg',
  })
  const [inputType, setInputType] = useState('password')
  const [error, setError] = useState('')
  const [icon, setIcon] = useState('eyeIcon')
  const [showValidationModal, setShowValidationModal] = useState(false)
  const [validationError, setValidationError] = useState('')
  const [confetti, setConfetti] = useState(false)
  const confettiConfig = {
    angle: 90,
    spread: 360,
    startVelocity: 20,
    elementCount: 130,
    dragFriction: 0.12,
    duration: 3000,
    stagger: 3,
    width: '7px',
    height: '7px',
    perspective: '500px',
    colors: ['#a864fd', '#29cdff', '#78ff44', '#ff718d', '#fdff6a'],
  }

  useEffect(() => {
    if (confetti) {
      const timer = setTimeout(() => setConfetti(false), 3000)
      return () => clearTimeout(timer)
    }
  }, [confetti])
  const dispatch = useDispatch()
  async function handleSubmit(e) {
    e.preventDefault()
    // console.log(e.target.value)
    if (LoginCred.username && LoginCred.password !== '') {
      // try {
      dispatch(doLogin(LoginCred))
      setConfetti(true)
      setTimeout(() => {
        navigate('/home');
      }, 800)
      // } catch (err) {
      //   console.log("catch")
      //   setError("email is error")
      //   // setShowValidationModal(true)
      //   // setValidationError('Invalid username or password')
      // }
    } else {
      setShowValidationModal(true)
      if (LoginCred.username === '' && LoginCred.password === '')
        setValidationError('   ')
      else if (LoginCred.username === '' && LoginCred.password !== '')
        setValidationError('     ')
      else setValidationError('    ')
    }
  }

  function handleNotEyeIconClicked() {
    setIcon('eyeIcon')
    setInputType('password')
  }

  function handleEyeIconClicked() {
    setInputType('text')
    setIcon('notEyeIcon')
  }

  function handleForgetPassOrUsername() {
    setShowValidationModal(true)
    setValidationError(
      <>
           :{" "}
        <a href="tel:046317803" style={{ textDecoration: "none" }}>
          <strong>04-6317803</strong>
        </a>
      </>
    );
  }


  function handleCloseModal() {
    setShowValidationModal(false)
  }

  return (
    <div className="login-signup-container" dir="rtl">
      <div className="form-container">
        <h2 className="login-header"> </h2>
        <form onSubmit={handleSubmit}>
          <div className="chat-people-img-container">
            <img
              className="chat-people-img"
              src="https://res.cloudinary.com/dmox9pnnx/image/upload/v1696758733/gapwfhpxqztjnxxgnfzc.png "
              alt="Chatpeople"
            />
            <img
              className="chat-people-img"
              src="https://res.cloudinary.com/dmox9pnnx/image/upload/v1696758733/gapwfhpxqztjnxxgnfzc.png "
              alt="Chatpeople"
            />
          </div>
          <div className="form-group">
            <input
              autoComplete='off'
              type="text"
              placeholder=" "
              value={LoginCred.username}
              onChange={(e) =>
                setLoginCred({ ...LoginCred, username: e.target.value })
              }
            />
          </div>

          <div className="form-group">
            <input
              autoComplete='off'
              type={inputType}
              placeholder=""
              value={LoginCred.password}
              onChange={(e) =>
                setLoginCred({ ...LoginCred, password: e.target.value })
              }
            />
            {icon === 'eyeIcon' ? (
              <EyeIcon onEyeIconClicked={handleEyeIconClicked} />
            ) : (
              <NotEyeIcon onEyeIconClicked={handleNotEyeIconClicked} />
            )}
          </div>

          <div className="form-group">
            <button className="login-button" type="submit">
              
              <Confetti active={confetti} config={confettiConfig} />
            </button>
          </div>
        </form>
        <div className='btns-group'>
          <p onClick={handleForgetPassOrUsername}>
            
          </p>
          <p onClick={handleForgetPassOrUsername} >
             
          </p>
        </div>
        <p>{error}</p>
        <img
          className="chat-bot-img"
          src="https://res.cloudinary.com/dmox9pnnx/image/upload/v1696758746/msizjglxuxnizpbtodia.png "
          alt="Chatbot"
        />
      </div>
      {showValidationModal && (
        <ValidationModal msg={validationError} onClose={handleCloseModal} />
      )}
    </div>
  )
}
