import { useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { setCurrStation } from '../store/actions/category.actions'


export function RobotDetails(props) {

    const params = useParams();
    // console.log
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const station = useSelector(
        (storeState) => storeState.categoryModule.currStation
    )
    console.log("station", station)

    useEffect(() => {
        loadStation()
    }, [params.id])

    function loadStation() {
        dispatch(setCurrStation(params.id))
    }

    function goToProducts(subId) {
        const queryParams = new URLSearchParams({
            subId: subId,
            catId: params.id,

        });
        navigate(`/products/?${queryParams.toString()}`)
        // navigate(`/products/${params.id}`)
    }

    if (!station) return (
        <div className="centered-container">
            <div className="loader"></div>
        </div>
    )

    return (
        <section className="robot-index">
            <section className="robot-list simple-cards-grid">
                {station.subCategories.map((subCategory) => (
                    <article
                        onClick={() => goToProducts(subCategory.subCategoryId)}
                        key={subCategory.subCategoryId}
                        className="catalog-preview pointer"
                    >
                        <div className="image-container">
                            <img className="preview-image" src={subCategory.subCategoryImgUrl} alt={subCategory.subCategoryName} />
                        </div>
                        <p className="preview-p">{subCategory.subCategoryNameInArabic}</p>
                    </article>
                ))}
            </section>
        </section>
    ); // Added this closing parenthesis
}
