import React, { useState } from 'react';
import { useDispatch } from 'react-redux';

import { uploadImg } from '../../services/upload.service';
import { addCategory, addSubCategory } from '../../store/actions/category.actions';
import { ValidationModal } from './ValidationModal';

export function AddSubCategoryModal({ closeModal }) {
    const [validationMsg, setValidationMsg] = useState('');
    const [formValues, setFormValues] = useState({
        categoryNameEnglish: "",
        subCategoryNameEnglish: '',
        subCategoryNameHebrew: '',
        subCategoryNameArabic: '',
        subCategoryNameFrench: '',
        subCategoryImgUrl: '',
    });

    const [isUploading, setIsUploading] = useState(false);

    const dispatch = useDispatch()

    const handleChange = (e) => {
        setFormValues({
            ...formValues,
            [e.target.name]: e.target.value,
        });
    };

    const handleFormSubmit = (e) => {
        e.preventDefault();
        const isEmpty = Object.values(formValues).some(x => x === '');
        if (isEmpty) {
            setValidationMsg('Please fill in all the fields.');
        } else {
            dispatch(addSubCategory(
                formValues.categoryNameEnglish,
                formValues.subCategoryNameEnglish,
                formValues.subCategoryNameHebrew,
                formValues.subCategoryNameArabic,
                formValues.subCategoryNameFrench,
                formValues.subCategoryImgUrl
            ));
            closeModal();
        }

    };

    const handelFile = async (e) => {
        const file = e.target.files[0];
        try {
            setIsUploading(true);
            const { url } = await uploadImg(file);
            console.log("url", url);
            setFormValues({ ...formValues, subCategoryImgUrl: url });
        } catch (err) {
            console.log('Error uploading image: ', err);
        } finally {
            setIsUploading(false);
        }
    };

    return (
        <>
            <div className="modal-backdrop" >
                <div className="modal-add" >

                    <div>
                        <button className='close-product-modal-btn' onClick={closeModal}>x</button>
                    </div>

                    <form className="modal-body" onSubmit={handleFormSubmit}>

                        <div className="image-section">
                            <label>
                                subCategory image url
                                <input
                                    type="text"
                                    name={"subCategoryImgUrl"}
                                    value={formValues.subCategoryImgUrl}
                                    onChange={handleChange}
                                />
                            </label>
                            <h1>OR</h1>
                            {/* <h1>Click the image to upload image</h1> */}
                            <label>
                                {isUploading ? (
                                    // <div className="centered-container-img">
                                    <span className="img-loader"></span>
                                    // </div>
                                ) : (
                                    <img
                                        src={formValues.subCategoryImgUrl || "https://www.free-videoconverter.net/images/free-online-image-enhancer/upload-image.png"}
                                        alt="category"
                                    />
                                )}

                                <input type="file" onChange={handelFile} className='hidden' />
                            </label>
                        </div>


                        <div className="category-details-section">
                            <h1>Subcategory details</h1>

                            <label>
                                Category Name in English
                                <input
                                    type="text"
                                    name="categoryNameEnglish"
                                    value={formValues.categoryNameEnglish}
                                    onChange={handleChange}
                                />
                            </label>

                            <label>
                                Subcategory Name in English
                                <input
                                    type="text"
                                    name="subCategoryNameEnglish"
                                    value={formValues.subCategoryNameEnglish}
                                    onChange={handleChange}
                                />
                            </label>

                            <label>
                                Subcategory Name in Hebrew
                                <input
                                    type="text"
                                    name="subCategoryNameHebrew"
                                    value={formValues.subCategoryNameHebrew}
                                    onChange={handleChange}
                                />
                            </label>

                            <label>
                                Subcategory Name in Arabic
                                <input
                                    type="text"
                                    name="subCategoryNameArabic"
                                    value={formValues.subCategoryNameArabic}
                                    onChange={handleChange}
                                />
                            </label>

                            <label>
                                Subcategory Name in French
                                <input
                                    type="text"
                                    name="subCategoryNameFrench"
                                    value={formValues.subCategoryNameFrench}
                                    onChange={handleChange}
                                />
                            </label>
                        </div>


                        <button className='add-product-modal-btn' type="submit">Add Subcategory</button>
                    </form>
                </div>
            </div>
            {validationMsg && <ValidationModal msg={validationMsg} onClose={() => setValidationMsg('')} />}

        </>
    );
}
